<template>
  <div class="global">
    <div class="login">
      <div class="head">欢迎登陆</div>
      <el-form
        ref="formRef"
        :model="form"
        :rules="rules"
        label-position="left"
        label-width="auto"
        size="normal"
      >
        <el-form-item label="" prop="username">
          <el-input
            v-model="form.username"
            placeholder="请输入用户名"
            prefix-icon="User"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            v-model="form.password"
            placeholder="请输入密码"
            prefix-icon="Key"
            show-password
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button
            type="primary"
            round
            plain
            style="width: 100%; text-align: center"
            @click="login"
            >login</el-button
          >
        </el-form-item>
        <el-button type="text" @click="gotoproflogin">专家登录入口==>></el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request.js";
export default {
  data() {
    return {
      form: {},
      rules: {
        username: [
          {
            required: true,
            message: "请输入用户名",
            trigger: "blur",
          },
        ],
        password: [
          {
            min: 6,
            max: 10,
            message: "Length should be 6 to 10",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    login() {
      //表单验证
      this.$refs["formRef"].validate((valid) => {
        if (valid) {
          // 验证通过执行请求
          request.post("/user/login", this.form).then((res) => {
            if (res.code == "0") {
              this.$message({
                type: "success",
                message: "登录成功",
              });
              console.log(res);
              sessionStorage.setItem("user", JSON.stringify(res.data)); //缓存用户信息
              this.$router.push("/sys/index"); //登录之后跳转
            } else {
              this.$message({
                type: "error",
                message: res.msg,
              });
            }
          });
        }
      });
    },
    gotoproflogin() {
      this.$router.push("/professorLogin")
    }
  },
};
</script>

<style>
.global {
  width: 100%;
  height: 100vh;
  background: rgb(113, 128, 216);
  overflow: hidden;
}

.login {
  width: 400px;
  margin: 200px auto;
}

.head {
  text-align: center;
  font-size: 30px;
  color: aliceblue;
  margin: 20px auto;
}
</style>